/*Como importar una fuente de Google Fonts:*/
/*
*EJEMPLO
*/
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

body a {
  text-decoration: 0; }

body p {
  text-align: center;
  padding: 0;
  margin: .5rem 0; }

body h1, body h2, body h3 {
  padding: 0;
  font-family: "Ephesis", cursive;
  font-size: 3.3rem; }

.contenedor {
  max-width: 60rem;
  width: 95%;
  margin: 0 auto; }

.rrss {
  margin-top: 1.3rem;
  display: flex; }
  .rrss svg {
    max-width: 2rem;
    max-height: 2rem;
    margin-right: 1.5rem; }
    .rrss svg path {
      fill: #64310F; }
  .rrss a:hover path {
    fill: #000; }

/*
*Para importar archivos del layout es lo mismo. Por ejemplo:
*@import 'layout/header';
*/
.menu {
  background-color: #FFBD59;
  height: 75px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #FFBD59;
  font-family: "Ephesis", cursive;
  color: #64310F; }
  .menu .logo {
    font-size: 35px;
    padding-left: 35px;
    font-weight: bold; }
  .menu .logo__enlace {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    text-decoration: none;
    color: #64310F; }
    .menu .logo__enlace p {
      font-size: 2.5rem;
      padding: 0;
      margin: 0;
      margin-left: .6rem; }
    .menu .logo__enlace picture {
      display: flex;
      align-items: center; }
    .menu .logo__enlace img {
      width: 3rem;
      height: 4rem; }

.menu_items {
  display: flex;
  list-style: none;
  margin-right: 30px; }
  .menu_items li {
    border-radius: 3px;
    margin: 0 5px; }
    .menu_items li a {
      font-family: "Pompiere", cursive;
      padding: 7px 13px;
      text-decoration: none;
      font-size: 20px;
      display: block;
      color: #64310F; }

.btn_menu {
  margin-right: 30px;
  font-size: 25px;
  color: #64310F;
  cursor: pointer;
  display: none; }

ul.mostrar {
  top: 65px;
  margin-top: 0;
  padding: 0;
  left: 0; }

@media screen and (max-width: 950px) {
  .menu {
    height: 65px; }
  .btn_menu {
    display: inline-flex; }
  .menu_items {
    position: fixed;
    width: 100vw;
    height: calc(100% - 65px);
    background: #FFDE59;
    top: 49px;
    left: -120vw;
    text-align: center;
    transition: all .8s;
    z-index: 2;
    flex-direction: column; }
  .menu_items li {
    margin: 30px 0 0 0;
    line-height: 30px; }
    .menu_items li:hover {
      background: none; }
    .menu_items li a {
      font-size: 20px;
      color: #64310F;
      font-weight: bold; }
      .menu_items li a:hover {
        color: #e9183b; }
  .menu .logo {
    font-size: 25px;
    padding-left: 0px; } }

@media screen and (max-width: 850px) and (orientation: landscape) {
  .menu li {
    margin: 5px 0 0 0; }
  .menu_items {
    overflow: scroll;
    height: calc(100%-65px); } }

header {
  background-color: #FFBD59; }

.header {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #FFBD59; }
  .header h1 {
    font-family: "Chau Philomene One", sans-serif;
    font-size: 3rem;
    text-transform: uppercase; }
  .header p {
    text-align: left;
    font-size: 1.5rem; }

.header__contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 35rem;
  font-family: "Ephesis", cursive;
  font-size: 1.3rem;
  margin-left: 3rem;
  margin-bottom: 7rem;
  z-index: 1; }

.imagen__pastel {
  background-image: url(/productolisto/img/header-pastel.webp);
  background-repeat: no-repeat;
  background-position: top left;
  background-size: cover; }

.waves {
  position: absolute;
  bottom: -.5rem;
  width: 100%; }

.productos {
  background-color: white;
  z-index: 3;
  margin-bottom: 50rem; }
  .productos h2 {
    text-align: center;
    font-size: 3.3rem;
    margin-bottom: 5rem; }

.productos__contenedor {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 2rem;
  transition: all .5s ease-in-out; }
  .productos__contenedor a:hover {
    color: #FFBD59;
    transform: scale(1.05);
    transform-origin: center center; }

.producto__lista {
  color: #fff;
  text-align: center;
  height: 18rem;
  display: flex;
  align-items: center;
  justify-content: center; }

.producto__lista-tortas {
  background: linear-gradient(90deg, rgba(2, 0, 36, 0.336) 0%, rgba(15, 15, 16, 0.5) 35%, rgba(122, 126, 127, 0.518943) 100%), url(/productolisto/img/producto__torta.webp);
  background-position: center center;
  background-size: cover; }

.producto__lista-dulces {
  background: linear-gradient(90deg, rgba(2, 0, 36, 0.336) 0%, rgba(15, 15, 16, 0.5) 35%, rgba(122, 126, 127, 0.518943) 100%), url(/productolisto/img/producto__dulces.webp);
  background-position: center center;
  background-size: cover; }

.producto__lista-postres {
  background: linear-gradient(90deg, rgba(2, 0, 36, 0.336) 0%, rgba(15, 15, 16, 0.5) 35%, rgba(122, 126, 127, 0.518943) 100%), url(/productolisto/img/producto__postres.webp);
  background-position: center center;
  background-size: cover; }
